
@import url('https://fonts.googleapis.com/css?family=Exo+2:100,400&subset=cyrillic');

@font-face {
  font-family: 'Trebuchet MS';
  src: url('../assets/fonts/Trebuchet-BoldItalic.eot');
  src: local('Trebuchet MS Bold Italic'), local('Trebuchet-BoldItalic'),
      url('../assets/fonts/Trebuchet-BoldItalic.eot?#iefix') format('embedded-opentype'),
      url('../assets/fonts/Trebuchet-BoldItalic.woff') format('woff'),
      url('../assets/fonts/Trebuchet-BoldItalic.ttf') format('truetype');
  font-weight: bold;
  font-style: italic;
}

@font-face {
  font-family: 'Trebuchet MS';
  src: url('../assets/fonts/TrebuchetMS-Bold.eot');
  src: local('Trebuchet MS Bold'), local('TrebuchetMS-Bold'),
      url('../assets/fonts/TrebuchetMS-Bold.eot?#iefix') format('embedded-opentype'),
      url('../assets/fonts/TrebuchetMS-Bold.woff') format('woff'),
      url('../assets/fonts/TrebuchetMS-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Trebuchet MS';
  src: url('../assets/fonts/TrebuchetMS-Italic.eot');
  src: local('Trebuchet MS Italic'), local('TrebuchetMS-Italic'),
      url('../assets/fonts/TrebuchetMS-Italic.eot?#iefix') format('embedded-opentype'),
      url('../assets/fonts/TrebuchetMS-Italic.woff') format('woff'),
      url('../assets/fonts/TrebuchetMS-Italic.ttf') format('truetype');
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: 'Trebuchet MS';
  src: url('../assets/fonts/TrebuchetMS.eot');
  src: local('Trebuchet MS'), local('TrebuchetMS'),
      url('../assets/fonts/TrebuchetMS.eot?#iefix') format('embedded-opentype'),
      url('../assets/fonts/TrebuchetMS.woff') format('woff'),
      url('../assets/fonts/TrebuchetMS.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}